*{box-sizing:border-box}html,body{height:100%;overflow:hidden}.icon-collection{display:inline-block;width:18px;height:18px;background:url("assets/icon_favorite_grey.png") no-repeat center center;background-size:contain}.icon-collection.white{background-image:url("assets/icon_favorite_white.png")}.icon-collection.checked{background-image:url("assets/icon_favorite.png")}.icon-back{display:inline-block;width:18px;height:18px;background:url("assets/left.png") no-repeat center center;background-size:contain}



